<template>
    <div class="layout">
        <Layout>
            <Header class="header"  >
                <Icon @click.native="handleSideShow" class="layout-list" type="navicon-round"></Icon>
                <div class="layout-nav">
                    <div class="layout-nav-content">
                        {{articleName}}
                    </div>
                </div>

            </Header>
            <Layout class="main-body">
                <Sider hide-trigger :class="['side-bar',sideShow?'side-bar_show':'side-bar_hide']">
                    <Menu
                        v-if="haveLists"
                        accordion
                        :active-name="haveLists ? articleLists[0].children[0].value:''"
                        theme="light" width="auto"
                        :open-names="[haveLists ? articleLists[0].value : '']"
                        @on-select="handleSideSelect"
                        >

                        <Submenu v-for=" list in articleLists" :key="list.value" :name="list.value">
                            <template slot="title">
                                {{list.label}}
                            </template>
                            <MenuItem v-for="(article, index) in list.children" :key="article.value" :name="article.value">{{(index+1)+"."+article.label}}</MenuItem>
                        </Submenu>
                    </Menu>
                </Sider>
                <Layout class="content-container" >
                    <Content class="content">
                        <div class="content-raw content-left-raw" v-if="false">
                            <Icon type="chevron-left"></Icon>
                        </div>
                        <mavon-editor :value="contents" :subfield="false" :toolbarsFlag="false" defaultOpen="preview" :ishljs="true"/>
                        <div class="content-raw content-right-raw" v-if="false">
                            <Icon type="chevron-right"></Icon>
                        </div>
                        <Spin size="large" fix v-if="contentShow"></Spin>
                    </Content>
                </Layout>
                <div @click="handleSideShow" v-show="sideShow" class="content-bg"></div>
            </Layout>
        </Layout>
    </div>
</template>

<script>
import {Layout,Header,Menu,MenuItem,Submenu,Content,Sider,Icon,Spin} from "iview"
import {getArticle,getArticleLists} from "../../api"
export default {
    data() {
        return {
            contents:"",
            articleLists:[],
            sideShow:false,
            contentShow:true,
            articleName:"",
        }
    },
    computed: {
        haveLists() {
            console.log(this.articleLists.length)
            return this.articleLists.length?true:false
        }
    },
    methods: {
        async getArticle(name) {
            this.contentShow = true;
            let data = await getArticle(name);
            this.contents = data.data;
            setTimeout(()=>{
                this.contentShow = false;
            },500)
        },
        async getArticleLists() {
            let data = await getArticleLists();
            this.articleLists = data.data;
            this.initArticle(data.data[0].children[0].value)
        },
        initArticle(name) {
            this.getArticle(name)
            this.getArticleName(name)
        },
        handleSideSelect(name) {
            this.getArticle(name)
            this.getArticleName(name)
        },
        handleSideShow() {
            this.sideShow = !this.sideShow
            console.log(this.sideShow)
        },
        getArticleName(key) {
            this.articleLists.forEach((ele)=>{
                ele.children.forEach((list)=>{
                    if(list.value === key) {
                        this.articleName = list.label
                    }
                })
            })
        }

    },
    mounted() {
        this.getArticleLists();
    },
    components: {
        Layout,
        Header,
        Menu,
        MenuItem,
        Submenu,
        Content,
        Sider,
        Icon,
        Spin
    }
}
</script>

<style lang="less" scoped>
@media all and (max-width:500px) {
    .layout {
        .header {
            position: fixed;
            top: 0;
            left: 0;
            z-index: 2000;
            .layout-list {
                display: inline-block;
            }
        }
        .content-container {
            padding: 0 24px 24px;
            .content {
                .content-left-raw {
                    top:50%;
                    margin-top: -25px;
                    left: 0px;
                }
                .content-right-raw {
                    top:50%;
                    margin-top: -25px;
                    right: 0px;
                }
            }

        }
        .content-bg {
            background: rgba(0,0,0,0.6);
            position: fixed;
            z-index: 1999;
            width:100%;
            height: 100%;
        }
        .side-bar {
            position: fixed;
            z-index: 2000;
        }
        .side-bar_hide {
            left: -200px;
        }
        .side-bar_show {
            left: 0px;
        }
        .main-body {
            margin-top: 63px;
        }
    }

}
@media all and (min-width:500px) {
    .header {
        .layout-list,.layout-nav {
            display: none;
        }
    }
    .content-container {
        padding: 0 60px 24px;
        .content {
            margin: 24px 50px;
            .content-left-raw {
                top:85%;
                left: 210px;
            }
            .content-right-raw {
                top:85%;
                right: 10px;
            }
        }
    }

}
.layout{
    height: 100%;
    border: 1px solid #d7dde4;
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    .header {
        background: #fff;
        padding:0;
        border-bottom: 1px solid #CCC;
        width: 100%;
        .layout-list {
            font-size: 24px;
            border-radius: 3px;
            position: absolute;
            top: 18px;
            left: 30px;

        }
        .layout-logo{
            width: 100px;
            height: 30px;
            background: #F0F0F0;
            border-radius: 3px;
            float: left;
            position: relative;
            top: 15px;
            left: 70px;
        }
        .layout-nav{
            margin-right: 80px;
            margin-left: 80px;
            &-content {
                text-align: center;
                text-overflow: ellipsis;
                overflow: hidden;
                white-space: nowrap;
                font-size: 16px;
            }

        }
    }
    .side-bar {
        background: #fff;
        height: 100%;
        padding-bottom: 40px;
        overflow-y: auto;

    }
    .content-container {
        .content {
            position: relative;
             min-height: 280px;
             background: #fff;
             margin: 24px 0;
             .ivu-spin-fix {
                 z-index: 1999;
             }
             .content-raw {
                 width: 40px;
                 height: 40px;
                 position: fixed;
                 z-index: 1999;
                 background: rgba(123,196,231,0.6);
                 border-radius: 50px;
                 text-align: center;
                 line-height: 40px;
                 color: #FFF;
                 cursor:pointer;
             }
        }
    }

}

</style>



